$bronze: #B3965B;
$red: #F34235;
$yellow: #FEC018;
$green: #4AAe51;
$green2: #139F85;
$green3: #95B02A;
$orange: #FD700D;
$purple: #77308B;
$header-height: 100px;

.ul-header {
  list-style-type: none;
  display: flex;
  flex-flow: row nowrap;
  background-color: $red;
  margin: 0;
  padding: 0;
  height: $header-height;
  align-items: center;
  justify-content: space-around; }

.li-header {
  margin-bottom: 1px; }

.header-fixed {
  position: fixed;
  width: 100%;
  z-index: 3;
  display: block; }

.bottom-eff {
  display: inline-block; }

.bottom-eff::after {
  display: block;
  content: '';
  height: 2px;
  background: black;
  width: 0px;
  transition: width 0.2s ease; }

.bottom-eff:hover::after {
  width: 100%; }

.img-header {
  width: 15vw;
  height: $header-height;
  cursor: pointer; }

.header-link {
  color: black;
  text-decoration: none !important;
  &:hover {
    color: black; }
  &:visited {
    color: black; } }

.logo-img {
  background-color: $red; }

.hamburger-icon {
  z-index: 3;
  position: fixed;
  left: 5px;
  top: 5px;
  display: none;
  cursor: pointer;
  padding: 20px;
  margin: 5px;
  & > * {
    width: 35px;
    height: 5px;
    background: white;
    border: 0.5px solid #bdbdbd;
    margin: 5px;
    border-radius: 3px;
    transition: transform 0.2s; } }

.clicked-hamburger {
  & > div:nth-child(1) {
    transform: rotate(45deg) translate(20%); }
  & > div:nth-child(2) {
    display: none; }
  & > div:nth-child(3) {
    transform: rotate(-45deg) translate(20%); } }

.hamburger-active {
  display: none;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  z-index: 2; }

.hamburger-active-display {
  display: block;
  overflow: hidden; }

@media only screen and (max-width: 992px) {
  .header-fixed {
    display: none; }

  .hamburger-icon {
    display: block; }

  .ul-header {
    flex-flow: column nowrap;
    background-color: white;
    height: 100vh;
    align-items: center;
    justify-content: space-around; }

  .span-header {
    width: 80vw;
    height: 18vh;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background 0.3s;
    &:hover {
      border: 1px solid $red;
      background: $orange; } } }
